<template>

  <b-modal
    id="modal_comedor_nutricion"
    ref="refModalSoporteForm"
    sv-model="dialog"
    :title="formTitle"
    size="lg"
    no-close-on-backdrop
    :hide-footer="true"
    no-close-on-esc
    hide-header-close
  >
    <b-form @submit.prevent="onSubmit">
      <b-form-group label="Motivo:">
        <b-form-textarea
          v-model="registroNutricion.motivo"
          placeholder="Describir motivo..."
          rows="3"
          required
          max-rows="6"
        />
      </b-form-group>
      <b-form-group label="Detalles:">
        <b-form-textarea
          v-model="registroNutricion.detalles"
          placeholder="Describir detalles..."
          rows="3"
          required
          max-rows="6"
        />
      </b-form-group>
      <b-form-group label="Conclusiones:">
        <b-form-textarea
          v-model="registroNutricion.conclusiones"
          placeholder="Describir conclusiones..."
          rows="3"
          required
          max-rows="6"
        />
      </b-form-group>

      <b-row class="mt-1">
        <b-col>
          <b-button
            variant="success"
            class="mb-1 mb-sm-0 mr-0 mr-sm-1"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            type="submit"
          >
            Guardar
          </b-button>
          <b-button
            variant="outline-secondary"
            class="mb-1 mb-sm-0 mr-0 mr-sm-1"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            @click="close"
          >
            Cerrar
          </b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-modal>

</template>

<script>
import {
  BRow, BCol, BForm, BModal, BFormGroup, BFormTextarea, BButton,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import {
  ref, watch, toRefs, computed,
} from '@vue/composition-api'
import store from '@/store'
// Notification
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    BRow,
    BCol,
    BModal,
    BForm,
    BFormGroup,
    BFormTextarea,
    BButton,
    // vSelect,
  },
  directives: {
    Ripple,
  },
  props: {
    persona: {
      type: Object,
      default() {
        return { }
      },
    },
    editedItem: {
      type: Object,
      default() {
        return { }
      },
    },
  },
  data() {
    return {
      selected: null,
      selectedEstado: null,
      optionsTipos: ['domiciliaria', 'hospitalaria', 'otros'],
    }
  },
  methods: {
    close() {
      this.$bvModal.hide('modal_comedor_nutricion')
    },
  },
  setup(props, { emit }) {
    const { persona } = toRefs(props)
    const { editedItem } = toRefs(props)

    const editedIndex = ref(-1)
    const toast = useToast()

    const blankregistroNutricion = {
      servidor_publico_id: 501,
      motivo: null,
      detalles: null,
      conclusiones: null,
    }

    const registroNutricion = ref(JSON.parse(JSON.stringify(blankregistroNutricion)))

    const resetuserData = () => {
      registroNutricion.value = JSON.parse(JSON.stringify(blankregistroNutricion))
    }

    const formTitle = computed(() => (editedIndex.value === -1 ? 'Nuevo Registro ' : 'Editar'))

    watch(persona, () => {
      resetuserData()
      registroNutricion.value.usuario_id = persona.value.usuario_id
      registroNutricion.value.persona = persona.value
      editedIndex.value = -1
    })

    watch(editedItem, () => {
      editedIndex.value = editedItem.value.id

      registroNutricion.value = editedItem.value
      registroNutricion.value.persona = editedItem.value.persona_usuario
      registroNutricion.value.servidor_publico_id = editedItem.value.servidor_publico_id
      registroNutricion.value.usuario_id = editedItem.value.usuario_id
    })

    const onSubmit = () => {
      if (editedIndex.value > -1) {
        store.dispatch('comedor-nutricion/updateNutricion', registroNutricion.value)
          .then(() => {
            toast({
              component: ToastificationContent,
              props: {
                title: 'Actualizacin exitosa',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            emit('refresh-data-visitas-table')
          })
          .catch(() => {
            toast({
              component: ToastificationContent,
              props: {
                title: 'Error en actualizacin',
                icon: 'AlertTriangleIcon',
                variant: 'danger',
              },
            })
          })
      } else {
        store.dispatch('comedor-nutricion/addRegistroNutricion', registroNutricion.value)
          .then(() => {
            toast({
              component: ToastificationContent,
              props: {
                title: 'Registro exitoso!',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            emit('refresh-data-visitas-table')
          })
      }
    }

    return {
      registroNutricion,
      resetuserData,
      onSubmit,
      formTitle,
    }
  },

}
</script>

